import React from 'react';
import { Link } from 'gatsby';
import { Container } from './nav.css';

const scrollToId = (e) => {
  const {
    id
  } = e.target.dataset;
  document.getElementById(id).scrollIntoView(true);
}
const Nav = () => (
  <Container>
    <ul>
      <li>
        <Link
          to="/"
          activeStyle={{ color: "#000000" }}>Home</Link>
      </li>
      <li>
        <Link to="/projects" activeStyle={{ color: "#000000" }}>Projects</Link>
      </li>
      <li>
        <Link to="/about" activeStyle={{ color: "#000000" }}>About</Link>
      </li>
    </ul>
  </Container>
);

export default Nav;
